.bg {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: fixed;
    top: 0;
}

.background-tint {
    background-color: rgba(0, 0, 0, 0.4); /* Tint color */
    background-blend-mode: multiply;
}


.greeting-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.time {
    font-size: 15rem;
    padding: 0;
    margin: 0
}

.drop-shadow {
    color: 0 35px 35px rgba(0, 0, 0, 0.25)
}

@media (min-width: 425px) {
    .greeting {
        font-size: 25px;
    }
}
